<template>
	<v-dialog :value="value" fullscreen hide-overlay transition="dialog-bottom-transition" persistent no-click-animation>
		<v-card class="d-flex flex-column">
			<!-- toolbar -->
			<div>
				<v-toolbar dark style="flex: auto">
					<v-btn icon dark @click="close_dialog">
						<v-icon>mdi-chevron-left</v-icon>
					</v-btn>
					<v-avatar class="ml-2">
						<img :src="require('../assets/buddha-icon.png')"/>
					</v-avatar>
					<v-toolbar-title>
						<v-list-item two-line>
							<v-list-item-content>
								<v-list-item-title class="text-h6">{{ UI.name }}</v-list-item-title>
								<v-list-item-subtitle style="position: relative;min-height: 16px; min-width: 100px">
									<v-fade-transition>
										<span class="top-left-corner" v-if="typing" key="1">{{ UI.status_typing }}</span>
										<span class="top-left-corner" v-else key="2">{{ UI.status_online }}</span>
									</v-fade-transition>
								</v-list-item-subtitle>
							</v-list-item-content>
						</v-list-item>
					</v-toolbar-title>
					<v-spacer></v-spacer>
				</v-toolbar>
			</div>
			<!-- conversation list -->
			<v-card-text class="flex-grow-1" style="position: relative">
				<div style="position: absolute; top: 10px; left: 0; width: 100%; height: 100%; overflow-y: auto">
					<!-- message -->
					<v-fade-transition group>
						<v-list-item dense v-for="message in conversation" :key="message.id">
							<v-list-item-content :id="message.id">
								<!-- text -->
								<v-list-item :class="get_list_class(message)">
									<v-card flat dark :color="get_bubble_color(message)" style="border-radius: 15px" max-width="70vw">
										<v-card-text class="body-1 py-2" style="color: white;">{{ message.text }}</v-card-text>
										<v-card-actions class="pt-0 px-4 justify-end caption"><span class="grey--text text--lighten-2">{{ message.time }}</span></v-card-actions>
									</v-card>
								</v-list-item>
								<!-- chips -->
								<v-list-item :class="get_list_class(message)" v-if="message.chips.length > 0">
									<v-chip-group dark>
										<v-chip color="grey" :class="get_list_class(message)" v-for="chip in message.chips" :key="chip" @click="click_chip(chip, message)">
											{{ chip }}
										</v-chip>
									</v-chip-group>
								</v-list-item>
							</v-list-item-content>
						</v-list-item>
					</v-fade-transition>
					<!-- typing -->
					<v-fade-transition hide-on-leave>
						<v-list-item dense v-if="typing">
							<v-list-item-content>
								<v-list-item>
									<v-card flat dark color="grey darken-3" style="border-radius: 15px">
										<v-card-text class="body-1 py-2" style="color: white;">
											<v-icon color="white">mdi-dots-horizontal</v-icon>
										</v-card-text>
									</v-card>
								</v-list-item>
							</v-list-item-content>
						</v-list-item>
					</v-fade-transition>
				</div>
			</v-card-text>
			<!-- text bar -->
			<div>
				<v-toolbar dark flat bottom>
					<div class="d-flex flex-grow-1 align-center" style="gap: 15px">
						<div class="flex-grow-1">
							<v-text-field v-model="text" autocomplete="off" filled rounded dense hide-details @keypress.enter="send(text)" autofocus></v-text-field>
						</div>
						<v-btn icon elevation="0" @click="send(text)" :disabled="!text">
							<v-icon color="primary">mdi-send</v-icon>
						</v-btn>
					</div>
				</v-toolbar>
			</div>
		</v-card>
	</v-dialog>
</template>

<script>
import moment from 'moment';

export default {
	name: "bd-fullscreen-dialog",
	props: {
		value: Boolean,
	},
	model: {
		prop: 'value',
		event: 'close',
	},
	data: () => {
		return {
			UI: {
				name: '佛祖',
				status_online: '在線上',
				status_typing: '輸入中...',
			},
			text: '',
			conversation: [],
			typing: false,
		}
	},
	mounted() {

	},
	methods: {
		close_dialog() {
			this.$emit('close', false);
		},
		get_list_class(message) {
			return message.buddha ? '' : 'justify-end';
		},
		get_bubble_color(message) {
			return message.buddha ? 'grey darken-3' : 'primary';
		},
		_uuid() {
			let d = Date.now();
			if (typeof performance !== 'undefined' && typeof performance.now === 'function') {
				d += performance.now(); //use high-precision timer if available
			}
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
				var r = (d + Math.random() * 16) % 16 | 0;
				d = Math.floor(d / 16);
				return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
			});
		},
		async show_typing(delay, typing) {
			if (delay > 0) {
				await new Promise(resolve => {
					setTimeout(function () {
						resolve();
					}, delay);
				});
			}
			this.typing = true;
			if (typing > 0) {
				await new Promise(resolve => {
					setTimeout(function () {
						resolve();
					}, typing);
				});
			}
			this.typing = false;
		},
		async click_chip(chip, message) {
			message.chips = [];
			await this.send(chip);
		},
		async send(text) {
			this.text = '';
			if (text) {
				await this.push_new_message(text);
				// 學舌鳥
				await this.push_new_message(('學舌鳥: ' + text), true, ['是', '否'], 1000, 1500);
			}
		},
		async push_new_message(text, buddha = false, chips = [], delay = 0, ms = 0) {
			await this.show_typing(delay, ms)
			let id = this._uuid();
			let new_message = {
				id: id,
				buddha: buddha,
				text: text,
				chips: chips,
				time: moment().format('HH:mm'),
			};
			this.conversation.push(new_message);
			await this.$nextTick();
			let el = document.getElementById(id);
			el.scrollIntoView({behavior: "smooth"});
		},
	},
}
</script>

<style scoped>
.top-left-corner {
	position: absolute;
	top: 0;
	left: 0;
}
</style>